.host {
  display: flex;
  flex-direction: row;
  align-items: center;

  line-height: 142.6%;
}
.hostName {
  width: 80px;
  font-size: 18px;
  color: #ff6060;
  margin: 2px 10px 0 0;
  text-align: right;
  font-weight: 500;
}
.profilPictureHost {
  width: 64px;
  height: 64px;
  border-radius: 50px;
}

@media (max-width: 740px) {
  .host {
    position: relative;
    justify-content: right;
    margin-bottom: 15px;
    padding-right: 0;
    line-height: 100%;
  }
  .hostName {
    width: 50px;
    font-size: 12px;
  }
  .profilPictureHost {
    width: 32px;
    height: 32px;
  }
}
